body {
  margin: 0;
  font-family: roboto, sans-serif;
}

main {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.movie {
  width: 300px;
  background-color: #e2dddd;
  border-radius: 5px;
  margin: 1rem;
  position: relative;
  overflow: hidden;
  box-shadow: .2px 4px 5px rgba(0, 0, 0, .1);
}

.movie img {
  width: 100%;
}

.movie-info {
  padding: 10px;
}

#release_date, #vote_average {
  color: #ff4500;
  font-size: 16px;
  font-weight: bold;
}

.movie-details {
  justify-content: space-between;
  align-items: baseline;
  display: flex;
}

.header {
  height: 230px;
  background-image: url("../Rectangle 1.c7c108fd.png");
  background-position: center;
  background-size: cover;
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.header-top {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-top: 50px;
  display: flex;
}

.header-top-left {
  padding-left: 50px;
}

.filmtitle {
  align-items: center;
  gap: 10px;
  text-decoration: none;
  display: flex;
}

.filmtitle:hover {
  cursor: pointer;
}

.film {
  width: 30px;
  height: 30px;
}

.page-title {
  color: #fff;
  margin: 0;
  padding-top: 1px;
  display: none;
}

.header-top-right {
  padding-right: 50px;
}

.mylibrary {
  gap: 20px;
  display: flex;
}

.navlink {
  justify-content: center;
  align-items: baseline;
  gap: 24px;
  display: flex;
}

.navbar .nav-links {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.active {
  color: #f5f5f5;
}

.ul-btn {
  gap: 24px;
  padding-left: 0;
  list-style-type: none;
  display: flex;
}

.libr, .homebtn {
  color: #fff;
  background: none;
  border: none;
  font-size: 12px;
  font-weight: bold;
}

.libr:hover, .homebtn:hover {
  cursor: pointer;
}

.header-link:after {
  content: "";
  width: 0;
  height: 3px;
  background-color: #fd9222;
  border-radius: 1px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: relative;
  top: 1px;
}

.header-link:hover:after, .header-link:focus:after {
  width: 100%;
}

.homebtn {
  padding-top: 1px;
  text-decoration: none;
}

.libr {
  text-decoration: none;
}

.header-bottom {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 5px;
  display: flex;
}

.search-button {
  cursor: pointer;
  color: #fff;
  background: none;
  border: none;
}

#search-input {
  color: #fff;
  background: none;
  border: none;
}

#search-input:focus {
  background: none;
  border: none;
  outline: none;
}

.searchform {
  background-color: none;
  color: #fff;
  width: 300px;
  background: none;
  border: none;
  justify-content: space-between;
  display: flex;
}

.srcline {
  width: 300px;
  height: 2px;
  color: #fff;
  display: flex;
}

.pagination {
  color: #0c0705;
  justify-content: center;
  align-items: center;
  margin: 10px 40px;
  display: flex;
}

.page {
  cursor: pointer;
  padding: 20px;
}

.page.disabled {
  cursor: not-allowed;
  color: gray;
}

.current {
  border: 5px solid #f30;
  border-radius: 25%;
  padding: 10px;
  font-size: 20px;
  font-weight: 600;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.lib-info {
  color: orange;
  gap: 5px;
  font-size: 12px;
  font-weight: bold;
}

.avg {
  color: #fff;
  background-color: orange;
  border-radius: 10px;
  padding: 5px 10px;
  font-size: 12px;
  font-weight: bold;
}

.lib-movie-details {
  justify-content: space-evenly;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header-top-left {
    padding-left: 80px;
  }

  .header-top-right {
    padding-right: 80px;
  }
}

@media screen and (min-width: 480px) {
  .page-title {
    display: flex;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.button-with-heading {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 10px;
  padding: 10px;
}

.button-with-heading:hover {
  background-color: gray;
}

.movie {
  background-color: #f0f0f0;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin: 10px;
  padding: 10px;
}

@media screen and (max-width: 600px) {
  #watched-movies {
    width: 95%;
  }

  #movies-list {
    grid-template-columns: 1fr;
  }
}

